.admin__layout {
  height: 100vh;
  display: grid;
  grid-template-columns: 250px 1fr;
  grid-template-rows: 100px 1fr;
}
.admin__layout header {
  grid-column: 2;
  grid-row: 1/2;
}
.admin__layout nav {
  grid-row: 1/3;
  grid-column: 1;
  height: 100%;
  position: static;
}
.admin__layout main {
  grid-column: 2;
  grid-row: 2/3;
}